:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #111;
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  line-height: 1.28571;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.global-title, .global-p {
  margin: 0;
}

.global-link {
  color: currentColor;
  text-decoration: none;
}

.global-button {
  color: currentColor;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  border: none;
}

.global-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-container {
  gap: var(--flex-item-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-item)  - 1) * var(--flex-item-gap) ) / var(--flex-item) );
}















